@charset "UTF-8";
html {
	box-sizing: border-box;
	overflow-y: scroll;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}
*,
::before,
::after {
	box-sizing: inherit;
}
::before,
::after {
	text-decoration: inherit;
	vertical-align: inherit;
}

* {
	background-repeat: no-repeat;
	padding: 0;
	margin: 0;
}
audio:not([controls]) {
	display: none;
	height: 0;
}

hr {
	overflow: visible;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}
small {
	font-size: 80%;
}
[hidden],
template {
	display: none;
}
abbr[title] {
	border-bottom: 1px dotted;
	text-decoration: none;
}
a {
	background-color: transparent;
	-webkit-text-decoration-skip: objects;
}
a:active,
a:hover {
	outline-width: 0;
}
code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
}

b,
strong {
	font-weight: bolder;
}

dfn {
	font-style: italic;
}
mark {
	background-color: #ff0;
	color: #000;
}
sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sub {
	bottom: -0.25em;
}
sup {
	top: -0.5em;
}
input {
	border-radius: 0;
}

[type="number"] {
	width: auto;
}

[type="search"] {
	-webkit-appearance: textfield;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}
textarea {
	overflow: auto;
	resize: vertical;
}
button,
input,
optgroup,
select,
textarea {
	font: inherit;
}
optgroup {
	font-weight: bold;
}
button {
	overflow: visible;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	border-style: 0;
	padding: 0;
}
button:-moz-focusring,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	outline: 1px dotted ButtonText;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
	-webkit-appearance: none;
}
button,
select {
	text-transform: none;
}
button,
input,
select,
textarea {
	background-color: transparent;
	border-style: none;
	color: inherit;
}
select {
	-moz-appearance: none;
	  -webkit-appearance: none;
}
select::-ms-expand {
	display: none;
}
select::-ms-value {
  color: currentColor;
}
legend {
  border: 0;
  color: inherit;
  display: table; /* 3 */
  max-width: 100%; /* 4 */
  white-space: normal; /* 5 */
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
img {
  border-style: none;
}
progress {
  vertical-align: baseline;
}
svg:not(:root) {
  overflow: hidden;
}
audio,
canvas,
progress,
video {
  display: inline-block;
}
@media screen {
  [hidden~="screen"] {
    display: inherit;
  }
  [hidden~="screen"]:not(:active):not(:focus):not(:target) {
    position: absolute !important;
    clip: rect(0 0 0 0) !important;
  }
}
[aria-busy="true"] {
  cursor: progress;
}
[aria-controls] {
  cursor: pointer;
}
[aria-disabled] {
  cursor: default;
}
::-moz-selection {
  background-color: #b3d4fc;
  color: #fff;
  text-shadow: none;
}
::selection {
  background-color: #b3d4fc;
  color: #fff;
  text-shadow: none;
}
h1, h2, h3, h4, h5, h6 {
font-weight: normal;
font-size: 100%;
}

